import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import VueGtm from 'vue-gtm'

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App),
  created () {
    this.pageTrack('首頁')
  },
  methods: {
    pageTrack (pageTitle = '') {
      if (Vue.gtm && pageTitle) {
        // 發送 content-view 事件給 GTM
        Vue.gtm.trackEvent({
          // 自行定義的事件名稱
          event: 'content-view',
          // 頁面功能/分頁標題文字
          pageTitle: `${pageTitle}`
        })
      }
    },
    clickAnswer (title = '', answer = '') {
      if (Vue.gtm && title && answer) {
        // 發送 content-view 事件給 GTM
        Vue.gtm.trackEvent({
          // 自行定義的事件名稱
          event: 'click-answer',
          // 頁面功能/分頁標題文字
          clickAnswer: `${title}（${answer})`
        })
      }
    },
    clickRightAnswer (title = '', answer = '') {
      if (Vue.gtm && title && answer) {
        // 發送 content-view 事件給 GTM
        Vue.gtm.trackEvent({
          // 自行定義的事件名稱
          event: 'click-answer-right',
          // 頁面功能/分頁標題文字
          clickRightAnswer: `${title}（${answer})`
        })
      }
    },
    questDone (rightTotal) {
      if (Vue.gtm && rightTotal) {
        // 發送 content-view 事件給 GTM
        Vue.gtm.trackEvent({
          // 自行定義的事件名稱
          event: 'quest-done',
          // 頁面功能/分頁標題文字
          questDoneCount: rightTotal
        })
      }
    }
  }
}).$mount('#app')

/** Facebook share START */
window.fbAsyncInit = () => {
  FB.init({
    appId: '627432957675792',
    xfbml: true,
    version: 'v3.2'
  })
  FB.AppEvents.logPageView()
}
