/** Google START */
Vue.use(VueGtm, {
  // container id
  id: 'GTM-WX6KGRR',
  // enable track or not (optional)
  enabled: process.env.NODE_ENV === 'production',
  // display console logs debugs or not (optional)
  debug: false
})
/** Google END */
