<template>
  <div id="page-home">
    <div v-show="isStart && !questIsDone">
      <img :src="require(`@/assets/image/title.png`)" alt="" class="title">
      <!-- 倒數計時，每題10秒,只有在作答時出現 -->
      <div class="time">你還有 <span>{{ currentQuestTimeLeft }}</span> 秒可以作答</div>
    </div>
    <div class="main">
      <!-- <p v-show="isLoading">載入中</p> -->
      <div v-show="!isStart && !questIsDone">
        <!-- 開始畫面 -->
        <div class="startbox">
          <h1>新春時事大挑戰 你是什麼豬？</h1>
          <img :src="require(`@/assets/image/cover.png`)" alt="">
          <h4>新春豬年到！快來測驗看看你會是哪一種豬豬！<br>
          我們會從題庫中隨機挑選10題，每一題都有10秒時間<br>其中包含了許多台灣的時事題和常識題<br>
          準備好了嗎？開始作答吧 !</h4>
          <a class="btn_start" @click="clickStart">開始挑戰</a>
          <!-- <router-link :to="{name: 'quest'}" class="btn_start">開始挑戰</router-link> -->
          <!-- <a href="laiphaute2_quest.html" class="btn_start">開始挑戰</a> -->
        </div>
      </div>
      <div v-show="isStart && !questIsDone">
        <img :src="require(`@/assets/image/pig.png`)" alt="" class="pig">
        <!-- 題目區塊(白色底) -->
        <div class="quesbox">
          <div class="ques">
            <div class="q">
              <h2>Q{{quest.nowNo}}：{{ questArray[quest.nowNo - 1].q }}</h2>
              <ul class="option">
                <!-- <li class="wrong">新台幣 500 元</li> -->
                <!-- <li class="active">新台幣 600 元</li> -->
                <li
                  v-for="quest in questArray[quest.nowNo - 1].o"
                  :key="quest.value"
                  :class="quest.className"
                  @click="clickTheAnswer(quest.value)">
                  {{ quest.label }}
                </li>
                <!-- 點到正確答案，li加上class="active" -->
                <!-- 點到錯誤答案，該li加上class="wrong"，正確答案加上class="active" -->
              </ul>
            </div>
            <!-- 點選答案後出現 -->
            <div class="btn_next" v-show="quest.isAnswer" :class="{'hidden': !quest.isAnswer}">
              <a v-show="quest.total !== quest.nowNo" @click="clickNextQuest">下一題</a>
              <a v-show="quest.total === quest.nowNo" @click="clickEndQuest">結束</a>
              <div v-show="quest.total !== quest.nowNo" class="hint">即將於 <span>{{ nextQuestCountDown }}</span> 秒後自動跳轉...</div>
            </div>
          </div>
          <div class="pic">
            <img :src="require(`@/assets/image/quest/${questArray[quest.nowNo - 1].i}`)">
          </div>
        </div>
      </div>
      <div class="endbox" v-show="questIsDone">
        <div id="image-save">
          <h1>新春大挑戰！你是什麼豬？</h1>
          <h2>你的答對率<span class="score">{{ rightCount * 10 }}%</span>，你是<span class="level">{{ result[rightCount].pigName }}</span>！</h2>
          <img v-if="questIsDone && result[rightCount]" :src="require(`@/assets/image/${result[rightCount].image}`)" alt="" class="endpic">
          <h4>
            <b>{{ result[rightCount].pigName }}：</b>
            {{ result[rightCount].description }}
          </h4>
        </div>
        <a v-show="isWeb" class="btn download" @click="clickDownload()">
          <img :src="require(`@/assets/image/down2.png`)" alt="下載圖片" title="點我下載豬豬圖片">
          <p>下載圖片</p>
        </a>
        <a class="btn share" @click="clickFacebookShare">
          <img :src="require(`@/assets/image/fb.png`)" alt="分享到FB" title="點我分享到FB">
          <p>馬上分享</p>
        </a>
        <a class="btn plurk share" @click="clickPlurkShare">
          <!-- <img :src="require(`@/assets/image/plurk.svg`)" alt="分享到Plurk" title="點我分享到Plurk"> -->
          <p>分享至噗浪</p>
        </a>
        <div class="lottery">
            <h5>新年加碼抽好獎！原創設計桌遊送給你！</h5>
            <a href="https://goo.gl/forms/eBuOB2VLwyrTQIj13" target="_blank">
            <img v-if="isWeb" :src="require(`@/assets/image/lottery.jpg`)" alt="新年加碼抽好獎！原創設計桌遊送給你！" title="點我參加桌遊抽獎活動">
            <img v-else :src="require(`@/assets/image/lottery-m.jpg`)" alt="新年加碼抽好獎！原創設計桌遊送給你！" title="點我參加桌遊抽獎活動">
          </a>
        </div>
        <a class="btn again" @click="reStart">
          <img :src="require(`@/assets/image/again.png`)" alt="再次挑戰" title="點我重新開始測驗">
          <p>再次挑戰！</p>
        </a>
      </div>
    </div>
    <div class="image-list">
      <template v-for="(quest, index) in questArray">
        <img v-if="!questIsDone" :key="index" :src="require(`@/assets/image/quest/${quest.i}`)" :alt="quest.q" @load="loaded(index)">
      </template>
    </div>
  </div>
</template>
<script>
import result from '@/result.json'
import html2canvas from 'html2canvas'
// import { timeout } from 'q'
export default {
  props: ['allQuestArray'],
  data () {
    return {
      isWeb: null,
      nextQuestSetTimeout: null,
      currentQuestSetTimeout: null,
      isLoading: false,
      isStart: false,
      totalNum: 10,
      questArray: [],
      currentQuestTimeLeft: 10,
      nextQuestCountDown: 5,
      questIsDone: false,
      quest: {
        total: 0,
        nowNo: 1,
        isRight: [],
        isAnswer: false,
        isAnswered: []
      },
      result: result
    }
  },
  computed: {
    isDone () {
      return this.quest.total < this.quest.nowNo
    },
    rightCount () {
      return this.questArray.filter(quest => quest.isRight).length
    }
  },
  created () {
    this.isWeb = screen.width >= 600
    this.getQuestArray()
  },
  methods: {
    getQuestArray () {
      let theQuestArray = []
      let questArray = JSON.parse(JSON.stringify(this.allQuestArray))
      for (let i = 0; i < this.totalNum; i++) {
        let index = Math.floor(Math.random() * questArray.length)
        let theQuest = questArray[index]
        theQuest.isLoaded = false
        let option = JSON.parse(JSON.stringify(theQuest.o))
        let optionArray = []
        for (let j = 0; j < option.length; j++) {
          if (Math.random() > .5) {
            optionArray.push(option[j])
          } else {
            optionArray.unshift(option[j])
          }
        }
        theQuest.o = JSON.parse(JSON.stringify(optionArray))
        theQuestArray.push(theQuest)
        questArray.splice(index, 1)
      }
      this.quest.total = theQuestArray.length
      this.questArray = JSON.parse(JSON.stringify(theQuestArray))
    },
    clickStart () {
      this.checkImgLoadDone()
      this.$root.pageTrack('開始')
      // go top
      window.scrollTo(0, 0)
    },
    loaded (index) {
      this.questArray[index].isLoaded = true
    },
    checkImgLoadDone () {
      this.isLoading = true
      let notLoaded = this.questArray.filter(quest => !quest.isLoaded)
      if (notLoaded.length > 0) {
        setTimeout(() => {
          this.checkImgLoadDone()
        }, 200)
      } else {
        window.scrollTo(0, 0)
        this.isLoading = false
        this.isStart = true
        this.currentQuestCountDown(this.quest.nowNo - 1)
      }
    },
    /** 點擊下一題按鈕 */
    clickNextQuest () {
      if (this.isDone) return
      this.quest.isAnswer = false
      this.currentQuestTimeLeft = 10
      this.nextQuestCountDown = 5
      this.currentQuestCountDown(this.quest.nowNo)
      this.$root.pageTrack(this.questArray[this.quest.nowNo - 1].q)
      if (this.quest.nowNo < this.quest.total) {
        this.quest.nowNo += 1
      }
      window.scrollTo(0, 0)
    },
    /** 點擊結束按鈕 */
    clickEndQuest () {
      this.questIsDone = true
      this.$root.questDone(this.rightCount)
      clearTimeout(this.nextQuestSetTimeout)
      clearTimeout(this.currentQuestSetTimeout)
      this.$root.pageTrack('結束')
    },
    /** 點擊答案 */
    clickTheAnswer (value) {
      if (this.quest.isAnswer) return
      this.nextQuestCountDown = 5
      let theQuest = this.questArray[this.quest.nowNo - 1]
      clearTimeout(this.nextQuestSetTimeout)
      clearTimeout(this.currentQuestSetTimeout)
      if (value > -1 && parseInt(theQuest.a) === parseInt(value)) {
        // 答對
        let theOptionIndex = theQuest.o.findIndex(option => parseInt(option.value) === parseInt(value))
        theQuest.o[theOptionIndex].className = 'active'
        theQuest.isRight = true
        this.questArray.splice(this.quest.nowNo - 1, 1, theQuest)
        // this.quest.isRight.push(theQuest.no)
        this.$root.clickRightAnswer(`【${this.questArray[this.quest.nowNo - 1].no}】 ` + this.questArray[this.quest.nowNo - 1].q, theQuest.o[theOptionIndex].label)
        this.$root.clickAnswer(`【${this.questArray[this.quest.nowNo - 1].no}】 ` + this.questArray[this.quest.nowNo - 1].q, theQuest.o[theOptionIndex].label)
      } else {
        // 答錯
        let clickAnswer = '時間到'
        if (value > -1) {
          // 有答案
          let selectedOptionIndex = theQuest.o.findIndex(option => parseInt(option.value) === parseInt(value))
          theQuest.o[selectedOptionIndex].className = 'wrong'
          theQuest.isRight = false
          clickAnswer = theQuest.o[selectedOptionIndex].label
        }
        let theOptionIndex = theQuest.o.findIndex(option => parseInt(option.value) === parseInt(theQuest.a))
        theQuest.o[theOptionIndex].className = 'active'
        this.questArray.splice(this.quest.nowNo - 1, 1, theQuest)
        this.$root.clickAnswer(`【${this.questArray[this.quest.nowNo - 1].no}】 ` + this.questArray[this.quest.nowNo - 1].q, clickAnswer)
      }
      this.quest.isAnswer = true
      this.quest.isAnswered.push(this.quest.nowNo)
      this.clickNextQuestCountDown(this.quest.nowNo)
    },
    /** 下一題倒數計時 */
    clickNextQuestCountDown (no) {
      if (parseInt(no) !== parseInt(this.quest.isAnswered.length) ||
        this.done ||
        parseInt(no) !== parseInt(this.quest.nowNo) ||
        parseInt(no) === parseInt(this.quest.total)
      ) return
      const self = this
      if (this.nextQuestCountDown > 0) {
        this.nextQuestSetTimeout = setTimeout(() => {
          self.nextQuestCountDown -= 1
          self.clickNextQuestCountDown(no)
        }, 1000)
      } else {
        clearTimeout(this.nextQuestSetTimeout)
        this.clickNextQuest()
      }
    },
    /** 答題倒數計時 */
    currentQuestCountDown (no) {
      if (parseInt(no) !== parseInt(this.quest.isAnswered.length) || this.isDone) return
      const self = this
      if (this.currentQuestTimeLeft > 0) {
        this.currentQuestSetTimeout = setTimeout(() => {
          self.currentQuestTimeLeft -= 1
          self.currentQuestCountDown(this.quest.nowNo - 1)
        }, 1000)
      } else {
        clearTimeout(this.currentQuestSetTimeout)
        this.clickTheAnswer(-1)
      }
    },
    /** 重新開始 */
    reStart () {
      let newQuest = {
        total: 0,
        nowNo: 1,
        isRight: [],
        isAnswer: false,
        isAnswered: []
      }
      this.quest = JSON.parse(JSON.stringify(newQuest))
      this.currentQuestTimeLeft = 10
      this.nextQuestCountDown = 5
      this.getQuestArray()
      this.isStart = false
      this.questIsDone = false
      window.scrollTo(0, 0)
    },
    /** 點擊圖片下載 */
    clickDownload () {
      html2canvas(document.querySelector('#image-save')).then(canvas => {
        this.saveAs(canvas.toDataURL(), `pigchallenge-laiphaute.png`)
      })
    },
    saveAs (uri, filename) {
      var link = document.createElement('a')
      if (typeof link.download === 'string') {
        link.href = uri
        link.download = filename
        // Firefox requires the link to be in the body
        document.body.appendChild(link)
        // simulate click
        link.click()
        // remove the link when done
        document.body.removeChild(link)
      } else {
        window.open(uri)
      }
    },
    clickFacebookShare () {
      let img = require(`@/assets/image/${this.result[this.rightCount].image}`)
      this.fbShare(img)
    },
    fbShare (imgUrl) {
      // if (!this.isWeb) {
      //   FB.ui({
      //     method: 'share',
      //     href: window.location.href,
      //   }, function(response){})
      //   let href = `https://www.facebook.com/dialog/share?
      //               app_id=627432957675792
      //               &display=touch
      //               &href=${window.location.href}`
      //   let a = document.createElement('a')
      //   a.href = href
      //   console.log(a)
      //   a.click()
      // } else {
      //   let fbOption = {
      //     method: 'feed',
      //     link: window.location.href,
      //     picture: window.location.origin + imgUrl,
      //     caption: `你是${this.result[this.rightCount].pigName}！${this.result[this.rightCount].description}`,
      //     title:  `我在「新春大挑戰！你是什麼豬？」得到${this.rightCount * 10}分，那你呢？`,
      //     description: `你是${this.result[this.rightCount].pigName}！${this.result[this.rightCount].description}`,
      //     message: `你是${this.result[this.rightCount].pigName}！${this.result[this.rightCount].description}`
      //   }
      let fbOption = {
        method: 'share_open_graph',
        action_type: 'og.likes',
        action_properties: JSON.stringify({
          object: {
            'og:url': window.location.href,
            'og:title': `我在「新春大挑戰！你是什麼豬？」得到${this.rightCount * 10}分，那你呢？`,
            'og:description': `你是${this.result[this.rightCount].pigName}！${this.result[this.rightCount].description}`,
            'og:image': window.location.origin + imgUrl
            // 'og:type': 'website',
          }
        })
      }
      FB.ui(
        fbOption,
        response => {
          // console.log(response)
        }
      )
      // }
    },
    clickPlurkShare () {
      let img = require(`@/assets/image/${this.result[this.rightCount].image}`)
      let href = `http://www.plurk.com/?qualifier=shares&status=${window.location.origin} (我在「新春大挑戰！你是什麼豬？」得到${this.rightCount * 10}分，那你呢？) \r\n ${window.location.origin}${img}`
      console.log(href)
      window.open(href)
    }
  }
}
</script>
<style lang="scss" scoped>
.image-list {
  position: absolute;
  opacity: 0;
  visibility: hidden;
  img {
    height: 0;
  }
}
#image-save {
  padding: 30px;
  border-radius: 20px;
  background-color: #fff;
}
.endbox {
  padding-top: 0;
  padding-right: 0;
  padding-left: 0;
  .share {
    &.plurk {
      background-color: #ec443a;
    }
  }
}
.quesbox {
  align-items: normal;
  .ques {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    h2 {
      margin-bottom: 10px;
    }
    .option {
      li {
        margin: 0 0 10px 0;
        cursor: pointer;
        user-select: none;
      }
    }
    @media screen and (min-width: 600px) {
      .btn_next {
        position: relative;
        a {
          margin: 0;
        }
        .hint {
          position: absolute;
          top: 100%;
          left: 50%;
          transform: translateX(-50%);
        }
      }
    }
  }
}
@media screen and (max-width: 600px) {
  #image-save {
    padding: 30px 20px;
  }
